  scrapeResults = () => {
    var res = fetch("http://localhost:8000/scrape")
        .then(response => {
          return response.json();
          
        }).then(data =>{
          console.log(data);
          var text = JSON.stringify(data, null, "\n");
          console.log(text);
          this.setState({scrapeRes: text});
          return text;
        });
  }
