import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { firestoreConnect } from 'react-redux-firebase';
import { Redirect } from 'react-router-dom';
import RangeInput from './RangeInput.js';
import SelectorInput from './SelectorInput.js';
import SelectorInputMajor from './SelectorInputMajor.js';
import { Modal, Button, Row, Col, Card } from 'react-materialize';

import firebase, { db } from "../../firebase";

class CollegeInfoScreen extends React.Component {
  constructor(props){
    super(props);
    this.filters={};
    this.currentItemSortCriteria = null;
    this.newItemSortCriteria = null;
    this.flipped = false;
  }

  state={
    locations: ["Northeast","Midwest","South","West"],
    majors: ["Accounting","Architecture","Art","Biochemistry","Biology","Chemistry","Computer Science","Economics","Engineering","English","Environmental Studies","Graphic Design","History","Law","Linguistics","Mathematics","Music","Nursing","Philosophy","Physics","Political Science","Pre Med","Psychology","Religion","Teaching"],
    colleges: {},
    admissionRateMin: null,
    filterName: '',
    admissionRateMax: null,
    costOfAttendanceMin: null,
    costOfAttendanceMax: null,
    sizeMin: null,
    sizeMax: null,
    major1: null,
    major2: null,
    location: null,
    rankingMin: null,
    rankingMax: null,
    sATMathMin: null,
    sATMathMax: null,
    sATEBRWMin: null,
    sATEBRWMax: null,
    aCTCompositeMin: null,
    aCTCompositeMax: null,
    filterType: "strict"
  }

  render() {
    let info = this.props.location.info;
    console.log(info);
    console.log(this.props.collegeInfo);
    if (!info) {
      return <Redirect to="/" />;
    }

    let infoCollege = info.temp;

    return (
      <div className="outlet profile-container">
        <div className="college-container">
          <div className="college-header">
            {infoCollege["Name"]}
          </div>
        </div>
      </div>
    );
  };
}

const mapStateToProps = (state, ownProps) => {
  return {
      collegeInfo: state.firestore.data
  };
};

export default compose(
  connect(mapStateToProps),
  firestoreConnect(props => {
    if (!props.location.info) 
    {
        return [];
    }
    else
    {
        let info = props.location.info.temp;
        let infoName = info["Name"];
        return [
        { collection: 'Colleges',
            where: ['Name', '==', infoName]
        }];
    }
}),
)(CollegeInfoScreen);